.Menu {
   position: fixed;
   color: #ffffff;
   font-size: 15px;
   margin: 30px 30px 0 15px;
   min-width: 135px;
}

.Menu ul {
   margin-top: 10px;
   margin: 0;
   padding: 0;
   list-style: none;
}

.Menu li {
   height: 30px;
   line-height: 2;
   cursor: pointer;
}

.Menu li.active {
   opacity: 0.5;
}

.Menu_name {
   margin-bottom: 10px;
}